$sky_color_dark: #1F2023;
$sky_color_light: #424472;



.light {
  height: 100%;
  width: 100%;
  &:after, &:before {
    content: "";
    display: block;
    position: absolute;
  }
  &:before {
    width: 120%;
    height: 120%;
    top: 40%;
    left: -25%;
    background: radial-gradient(48.58% 48.58% at 49.96% 59.07%, #927CFF 0%, rgba(142, 133, 190, 0.4) 43.23%, rgba(59, 61, 70, 0) 100%);
  }
  &:after {
    width: 120%;
    height: 100%;
    top: 65%;
    left: 25%;
    background: radial-gradient(48.58% 48.58% at 49.96% 59.07%, #FFF38B 0%, rgba(180, 174, 113, 0.45) 38.02%, rgba(59, 61, 70, 0) 100%);
  }
}

.star1 {
  animation: 3s linear 1s infinite alternate twinkle;
}

.star2 {
  animation: 4s linear 0s infinite alternate twinkle;
}

.star3 {
  animation: 3.5s linear 0.5s infinite alternate twinkle;
}

.star4 {
  animation: 1.25s linear 1.5s infinite alternate twinkle;
}

.star5 {
  animation: 4s linear 2s infinite alternate twinkle;
}

.star6 {
  animation: 2.5s linear 1s infinite alternate twinkle;
}

@keyframes twinkle {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
p {
  // first we make all instances of this transparent. 
  opacity: 0; 
  // as we're using the same animation for each instance, call it here so we're not repeating it
  animation: fadeInUp 1s ease-in-out 0s forwards;

  // then define the animation delay in each class
  &.first {
    animation-delay: 1s;
  }
  &.second {
    animation-delay: 2s;
  }
  &.third {
    animation-delay: 3s;
  }
  &.fourth {
    animation-delay: 4s;
  }
  &.fifth {
    animation-delay: 6s;
  }
  &.sixth {
    animation-delay: 11s;
  }
 }
 .glow {
  font-size: 50px;
  animation: glow 1s ease-in-out infinite alternate;
}



.bg-before-white {
  position: relative;
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.3);
  margin-top: 40px;
  border-radius: 12px;
  overflow: hidden;
}

@media screen and (max-width: 1024px) {
  .bg-before-white { 
    padding: 8px;
    margin-top: 30px;
  }

}

@media screen and (min-width: 1024px) {
  .bg-before-white { 
    padding: 8px 24px ;
   
  }
}

.bg-before-white + .bg-before-white {
  margin-left: 40px;
}

.bg-before-white::before {
  content: "";
  display: block;
  background-color: #fff;
  opacity: 10%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

@keyframes glow {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #e60073, 0 0 40px #e60073, 0 0 50px #e60073, 0 0 60px #e60073, 0 0 70px #e60073;
  }
  
  to {
    text-shadow: 0 0 20px #fff, 0 0 30px #ff4da6, 0 0 40px #ff4da6, 0 0 50px #ff4da6, 0 0 60px #ff4da6, 0 0 70px #ff4da6, 0 0 80px #ff4da6;
  }
}
.animate-spinn {
  animation: spin 20s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

